
.dowloand {
  &__row {
    display: flex;
    justify-content: space-between;

    &:not(:last-child) {
      margin-bottom: 12px;
    }
  }

  &__col1 {
    flex: 0 1 66%;
  }

  &__col2 {
    display: flex;
    flex-shrink: 0;
    flex-basis: 32%;
    align-items: center;
    justify-content: center;
  }

  &__text {
    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }

  &--main {
    padding: 53px 0 70px;

    @include mw2 {
      padding: 35px 0 45px;
    }

    @include mw4 {
      padding: 20px 0 25px;
    }
  }

  @include mw2 {
    &__col1 {
      flex: 1 1 auto;
    }

    &__col2 {
      flex-basis: 20%;
    }
  }

  @include mw4 {
    &__row {
      display: block;
    }

    &__col1 {
      margin-bottom: 7px;
    }

    &__col2 {
      justify-content: flex-start;
    }
  }
}

.dowloand-link {
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;

  &__icon {
    position: relative;
    display: inline-block;
    margin-bottom: 9px;
    border-radius: 50%;
    width: 64px;
    height: 64px;
    background-color: $dodger-blue;

    &::before {
      content: '';
      position: absolute;
      left: 50%;
      top: 50%;
      width: 21px;
      height: 26px;
      background-position: center center;
      background-size: cover;
      background-repeat: no-repeat;
      transform: translate(-50%, -50%);
    }
  }

  &__icon--pdf {
    &::before {
      background-image: url('../images/svgSprite/svgSprite.svg#pdf');
    }
  }

  &__dscr {
    display: inline-block;
    width: 170px;
    line-height: 1.2;
  }

  @include mw2 {
    &__icon {
      margin: 0;
    }

    &__dscr {
      display: none;
    }
  }

  @include mw4 {
    flex-direction: row;

    &__icon {
      width: 50px;
      height: 50px;
    }

    &__dscr {
      display: inline-block;
      max-width: 130px;
      font-size: $fontsmallmob;
    }
  }
}
