.hero {
  background-image: url('../images/main-bg.jpg');
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: rgba(255, 255, 255, 0.1);

  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 10.5% 5px 16%;
    min-height: 330px;
    max-height: 575px;
  }

  &__title {
    margin-bottom: 25px;
    max-width: 650px;
    line-height: 1.15;
    text-align: center;
    text-shadow: 0 2px 2px rgba($color: #000, $alpha: 0.45);
  }

  &__descr {
    max-width: 444px;
    text-align: center;
    text-shadow: 0 2px 2px rgba($color: #000, $alpha: 0.45);
  }
}
