.top-news {
  &__list {
    display: flex;
    justify-content: space-between;
    margin: 0 -10px;

    @include mw2 {
      margin: 0 -5px;
    }

    @include mw5 {
      margin: 0;
    }
  }

  &__item {
    flex-basis: calc(100% / 3 - 20px);
    margin: 0 10px;
  }

  &__btn {
    margin: 23px 0 0;

    @include mw2 {
      margin: 15px 0 0;
    }

    @include mw4 {
      margin: 10px 0 0;
    }
  }
}

.top-news-scroller {
  @include mw2 {
    padding-right: 20px;
  }

  @include mw5 {
    margin-left: -5px;
  }
}
