
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html,
body {
  position: relative;
  overflow-x: hidden;
}

body {
  &.lock {
    overflow: hidden;
  }
}

.global-wrapper {
  display: flex;
  flex-direction: column;
  padding-top: 97px;
  min-height: 100vh;
  transform: translate3d(0, 0, 0);
  transition: transform 0.2s linear;

  @include mw2 {
    padding-top: 70px;

    &.active {
      transform: translate3d(65%, 0, 0);
    }
  }

  @include mw4 {
    &.active {
      transform: translate3d(80%, 0, 0);
    }
  }
}

.disable-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
}

.overlay {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 15;
  width: 100%;
  height: 200%;
  background-color: rgba(0, 0, 0, 0.75);
  opacity: 0;
  visibility: hidden;
  transition: visibility 0.3s linear, opacity 0.2s linear;

  @include mw2 {
    &.active {
      opacity: 1;
      visibility: visible;
    }
  }
}

.main {
  display: block;
  flex: 1 1 auto;
}

a {
  display: inline-block;
  text-decoration: none;
  color: inherit;
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

img {
  display: block;
  width: 100%;
  height: 100%;
  max-width: 100%;
}

button {
  border: none;
  padding: 0;
  font-family: inherit;
  background-color: transparent;
  cursor: pointer;
}

input {
  border: none;
  font-family: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
  font-weight: inherit;
  font-size: inherit;
  color: inherit;
}

body {
  font-family: 'Roboto', 'Arial', sans-serif;
  font-weight: 400;
  font-size: $fontnormal;
  line-height: 1.4;
  color: $textcolor;
  background-color: $mainbgcolor;
}

.section {
  @include mw1 {
    padding: 15px 0 70px;
  }

  @include mw2 {
    padding: 15px 0 50px;
  }

  @include mw3 {
    padding: 10px 0 45px;
  }

  @include mw4 {
    padding: 7 0 38px;
  }
}

.visually-hidden {
  position: absolute !important;
  overflow: hidden;
  border: 0 !important;
  padding: 0 !important;
  width: 1px !important;
  height: 1px !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
}

.container {
  margin: 0 auto;
  padding: 0 10px;
  max-width: 924px;

  @include mw2 {
    padding: 0 5px;
  }
}

.container-fluid {
  margin: 0 auto;
  padding: 0 5px;
  max-width: 1980px;
}

.main-title {
  font-weight: 900;
  font-size: $fontbigtitle;
}

.main-descr {
  font-weight: 300;
  font-style: italic;
  font-size: $fontnormal;
}

.mid-title {
  padding: 30px 0;
  font-weight: 900;
  font-size: $fontsemititle;
  color: $textcolor;

  @include mw2 {
    padding: 15px 0 12px;
  }

  @include mw4 {
    padding: 10px 0 7px;
  }
}

.small-title {
  font-weight: 500;
  font-size: $fontsmalltitle;
  line-height: 1.3;

  @include mw2 {
    line-height: 1.2;
  }
}

.main-text {
  font-weight: 300;
  font-size: $fontnormal;

  @include mw2 {
    font-size: 16px;
    line-height: 1.25;
  }
}

.small-text {
  font-weight: 300;
  font-size: $fontsmall;
  line-height: 1.4;
}

.padding {
  padding: 15px 0;

  @include mw2 {
    padding: 10px 0;
  }

  @include mw3 {
    padding: 5px 0;
  }

  @include mw3 {
    padding: 2px 0;
  }
}

.all-news-btn {
  display: block;
  border-radius: 10px;
  padding: 15px;
  width: 100%;
  min-height: 50px;
  box-shadow: $shbtn;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.3;
  text-align: center;
  background-color: $dodger;
}

.scroller {
  @include mw2 {
    overflow-x: scroll;
    overflow-y: hidden;
  }
}

.news-date {
  width: 100%;
  max-width: 80px;
  font-weight: 500;
  font-size: $fontsmall;
}

.comeback {
  text-decoration: underline;
  color: $anakiwa;
}