.breadcrumbs {
  display: flex;
  flex-wrap: wrap;

  &__item {
    &:not(:last-child) {
      margin-right: 12px;
    }

    &:not(:first-child) {
      .breadcrumbs__link {
        padding-left: 17px;

        &::before {
          content: '/';
          position: absolute;
          left: 0;
          top: 0;
        }
      }
    }

    .breadcrumbs__link {
      color: $anakiwa;
    }

    &:last-child {
      .breadcrumbs__link {
        text-decoration: none;
        color: $textcolor;
      }
    }
  }

  &__link {
    position: relative;
    white-space: nowrap;
    text-decoration: underline;
  }

  @include mw3 {
    flex-wrap: nowrap;
    overflow-x: scroll;
    overflow-y: hidden;
  }
}
