@mixin hover {
  @media (min-width: (768px)) {
    @content;
  }
}

@mixin mw1 {
  @media (max-width: (925px)) {
    @content;
  }
}

@mixin mw2 {
  @media (max-width: (801px)) {
    @content;
  }
}

@mixin mw3 {
  @media (max-width: (675px)) {
    @content;
  }
}

@mixin mw4 {
  @media (max-width: (485px)) {
    @content;
  }
}

@mixin mw5 {
  @media (max-width: (391px)) {
    @content;
  }
}
