.decorline {
  display: flex;
  align-items: center;
  justify-content: space-between;

  &::after {
    content: '';
    display: block;
    width: 100%;
    height: 1px;
    background-color: $textcolor;
    opacity: 0.2;
  }

  &__text {
    display: block;
    padding-right: 14px;
    white-space: nowrap;
  }
}