.all-news {
  &__title {
    margin-bottom: 8px;

    @include mw3 {
      margin-bottom: 0;
    }
  }

  &__decorline {
    margin-bottom: 33px;

    @include mw1 {
      margin-bottom: 25px;
    }

    @include mw2 {
      margin-bottom: 20px;
    }

    @include mw3 {
      margin-bottom: 15px;
    }

    @include mw4 {
      margin-bottom: 10px;
    }
  }
}

.all-news__actual {
  margin-bottom: 60px;

  @include mw2 {
    margin-bottom: 35px;
  }

  @include mw3 {
    margin-bottom: 25px;
  }

  @include mw4 {
    margin-bottom: 20px;
  }

  @include mw5 {
    margin-bottom: 15px;
  }
}

.all-news__archive {
  margin-bottom: 37px;

  @include mw2 {
    margin-bottom: 30px;
  }

  @include mw3 {
    margin-bottom: 25px;
  }

  @include mw4 {
    margin-bottom: 21px;
  }

  @include mw5 {
    margin-bottom: 15px;
  }
}