.header {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 50;
  padding: 31px 0;
  width: 100%;
  background-color: $mainbgcolor;
  visibility: visible;
  transition: top 0.2s linear, visibility 0.3s linear;

  &__container {
    position: relative;
    display: flex;
    align-items: center;
  }

  &__logo {
    flex: 0;
    margin-right: 35px;
  }

  &__nav {
    flex: 1 1 auto;

    &--mob {
      display: none;
      text-align: center;
    }
  }

  &__authorization {
    margin-right: 18px;
    text-align: right;
  }

  &__burger {
    position: absolute;
    right: 0;
    top: 50%;
    z-index: 10;
    transform: translate(-50%, -50%);
  }

  &.hide {
    top: -20%;
    visibility: hidden;
  }

  @include mw2 {
    padding: 18px 0;

    &__nav {
      display: none;

      &--mob {
        display: block;
        padding-right: 60px;
        font-weight: 700;
        line-height: 1.34;
        text-align: right;
      }
    }

    &__authorization {
      display: none;
    }

    &__burger {
      display: block;
    }

    &__logo {
      margin-right: 10px;
    }
  }

  @include mw4 {
    &__logo {
      margin-right: 5px;
    }

    &__burger {
      transform: translate(-25%, -50%);
    }

    &__nav--mob {
      li {
        &:not(:last-child) {
          margin-right: 8px;
        }
      }
    }
  }

  @include mw5 {
    &__language {
      display: none;
    }

    &__nav--mob {
      padding-right: 50px;
    }
  }
}

.logo {
  display: inline-flex;
  align-items: center;

  &__img1 {
    margin-right: 6px;
    width: 31px;
    height: 31px;
    fill: $anakiwa;
  }

  &__img2 {
    width: 82px;
    height: 20px;
    fill: $textcolor;
  }

  @include mw4 {
    &__img1 {
      margin-right: 3px;
      width: 19px;
      height: 19px;
    }

    &__img2 {
      width: 51px;
      height: 13px;
    }
  }

  @include mw5 {
    &__img1 {
      width: 23px;
      height: 23px;
    }

    &__img2 {
      width: 55px;
      height: 14px;
    }
  }
}

.nav {
  &__list {
    display: inline-flex;
  }

  &__item {
    span {
      display: block;
    }

    &:not(:last-child) {
      margin-right: 35px;
    }
  }

  &__link {
    font-size: 12px;

    @include hover {
      &:hover {
        text-decoration: underline;
      }
    }
  }

  @include mw2 {
    &__link {
      font-size: 12px;
    }
  }
}

.authorization {
  &__link {
    display: block;
    font-size: 12px;

    @include hover {
      &:hover {
        text-decoration: underline;
      }
    }
  }
}

.language {
  display: inline-flex;

  label {
    display: flex;
    align-items: center;
    justify-content: center;

    &:not(:last-child) {
      margin-right: 6px;
    }
  }

  &__control {
    position: relative;
    width: 24px;
    height: 24px;
    opacity: 0.3;
    transition: opacity 0.2s linear;
    cursor: pointer;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      background-position: center center;
      background-size: cover;
      background-repeat: no-repeat;
    }

    &:checked {
      opacity: 1;
    }

    &--ru {
      &::before {
        background-image: url('../images/svgSprite/svgSprite.svg#ru');
      }
    }

    &--eu {
      &::before {
        background-image: url('../images/svgSprite/svgSprite.svg#eu');
      }
    }

    @include hover {
      &:hover {
        opacity: 1;
      }
    }
  }

  @include mw2 {
    label {
      width: 32px;
      height: 32px;

      &:not(:last-child) {
        margin-right: 0;
      }
    }
  }
}
