.news-grid {
  &__list {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -10px;

    @include mw2 {
      margin: 0 -5px;
    }

    @include mw5 {
      margin: 0;
    }

    @media (max-width: 550px) {
      justify-content: center;
    }
  }

  &__item {
    flex-basis: calc(100% / 3 - 20px);
    margin: 0 10px 20px;

    @include mw1 {
      flex: 0 1 calc(100% / 2 - 20px);
      max-width: none;
    }

    @include mw2 {
      margin-bottom: 10px;
    }

    @include mw4 {
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}
