.movie {
  padding: 47px 0 106px;
  background-image: url('../images/movie/background.jpg');
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;

  &__inner {
    display: flex;
  }

  &__player {
    flex: 1 1 68%;
    overflow: hidden;
    border-radius: 10px 0 0 10px;
  }

  &__descr {
    flex: 0 0 32%;
  }

  @include mw3 {
    &__inner {
      flex-wrap: wrap;
    }

    &__player {
      flex: 1 1 100%;
      border-radius: 10px 10px 0 0;
      min-height: 330px;
    }

    &__descr {
      flex: 1 1 100%;
      border-radius: 0 0 10px 10px;
    }
  }
}

.description {
  overflow: hidden;
  border-radius: 0 10px 10px 0;
  padding: 56px 28px 47px 32px;
  background-color: $description;

  &__title {
    margin-bottom: 15px;
    font-weight: 500;
    font-size: $fontnormal;
    line-height: 1.2;
  }

  &__text {
    margin-bottom: 40px;
    font-weight: 300;
    font-size: 15px;
    line-height: 1.4;
  }

  @include mw1 {
    padding: 45px 25px;

    &__title {
      margin-bottom: 10px;
    }

    &__text {
      margin-bottom: 35px;
    }
  }

  @include mw2 {
    padding: 25px 20px;

    &__title {
      margin-bottom: 5px;
    }

    &__text {
      margin-bottom: 25px;
    }
  }

  @include mw3 {
    padding: 15px;

    &__text {
      margin-bottom: 15px;
    }
  }
}
