
.card {
  flex-basis: calc(100% / 3 - 20px);
  margin: 0 10px;

  &__article {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    overflow: hidden;
    border-radius: 10px;
    padding: 45px 15px 15px;
    height: 100%;
    min-height: 294px;
    background-color: rgba(255, 255, 255, 0.1);
  }

  &__images {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      z-index: 1;
      display: block;
      width: 100%;
      height: 100%;
      background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.4) 100%);
    }
  }

  &__date {
    position: relative;
    z-index: 4;
    padding: 5px 0;
  }

  &__title {
    position: relative;
    z-index: 2;
    max-width: 250px;
    font-size: 22px;
    line-height: 1.2;
  }

  &__link {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 3;
  }

  @include mw2 {
    flex: 0 0 calc(100% / 2 - 10px);
    margin: 0 5px;
  }

  @include mw3 {
    min-height: 285px;
  }

  @include mw4 {
    flex: 0 0 85%;
    min-height: 255px;

    &__title {
      font-size: 20px;
    }
  }

  @include mw5 {
    flex: 0 0 100%;
  }

  @media (max-width: 550px) {
    flex: 0 0 70%;
  }
}
