
.burger {
  z-index: 20;
  display: none;
  width: 33px;
  height: 22px;
  cursor: pointer;

  span,
  &::before,
  &::after {
    position: absolute;
    left: 0;
    width: 100%;
    height: 3px;
    background-color: $textcolor;
    transition: transform 0.2s linear, opacity 0.25s linear;
  }

  &::before,
  &::after {
    content: '';
  }

  &::before {
    top: 0;
  }

  &::after {
    bottom: 0;
  }

  span {
    top: 50%;
    transform: translateY(-50%);
  }

  &.active {
    &::before {
      top: 40%;
      transform: rotate(45deg);
    }

    &::after {
      bottom: 45%;
      transform: rotate(-45deg);
    }

    span {
      opacity: 0;
      transform: translate3d(200%, 0, 0);
    }
  }
}

.burger-menu {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 20;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  padding: 35px 20px 75px;
  width: 66%;
  height: 100vh;
  text-align: center;
  background-color: $mainbgcolor;
  visibility: hidden;
  transform: translate3d(-100%, 0, 0);
  transition: transform 0.2s linear, visibility 0.3s linear;

  &__nav {
    display: block;
    flex: 1 1 auto;
    margin-bottom: 20px;

    .nav__list {
      display: block;
    }

    .nav__item {
      &:not(:last-child) {
        margin-right: 0;
        margin-bottom: 15px;
      }
    }

    .nav__link {
      font-weight: 400;
      font-size: $fontnormal;
    }
  }

  @include mw2 {
    &.active {
      visibility: visible;
      transform: translate3d(0%, 0, 0);
      transition: transform 0.2s linear, visibility 0.3s;
    }
  }

  @include mw4 {
    width: 80%;
  }
}

.burger-menu-authorization {
  margin-bottom: 30px;

  &__link {
    display: block;
    font-size: $fontnormal;
    line-height: 1.8;
    text-align: center;
  }
}

.burger__language {
  display: none;
  flex: 0 0 auto;
  justify-content: center;

  @include mw5 {
    display: flex;
  }
}
