.footer {
  padding: 44px 0 25px;
  background-color: $footer;

  &__row {
    display: flex;
    justify-content: space-between;
    margin-bottom: 55px;
  }

  &__col {
    &--1 {
      flex: 0 1 335px;
      padding-right: 10px;
    }

    &--2 {
      flex: 0 1 290px;
    }
  }

  &__title {
    margin-bottom: 5px;
    font-weight: 300;
    font-size: 16px;
    line-height: 1.4;
  }

  &__social {
    padding: 13px 0;
  }

  @include mw2 {
    padding: 25px 0 20px;

    &__row {
      margin-bottom: 25px;
    }
  }

  @include mw4 {
    &__row {
      flex-wrap: wrap;
      margin-bottom: 10px;
    }

    &__col {
      &--1 {
        margin-bottom: 12px;
        padding-right: 0;
      }
    }

    &__title {
      margin-bottom: 3px;
    }

    &__social {
      padding: 0;
    }
  }
}

.contacts {
  &__item {
    line-height: 1.1;
  }
}

.social {
  display: flex;

  &__item {
    &:not(:last-child) {
      margin-right: 8px;
    }
  }

  &__link {
    transition: transform 0.1s linear;

    svg {
      width: 24px;
      height: 24px;
    }

    @include hover {
      &:hover {
        transform: scale(1.1);
      }
    }
  }
}

.copyright {
  &__links {
    margin-bottom: 10px;
  }

  &__link {
    text-decoration: underline;
  }

  @include mw4 {
    font-size: $fontsmallmob;

    &__link {
      line-height: 1.5;
    }
  }
}
